@import url(https://fonts.googleapis.com/css2?family=Exo:wght@400;500;600;700;800;900&display=swap);
.bg-orange {
  border-color: rgb(255, 115, 0) !important;
}

.bg-red {
  border-color: rgb(153, 0, 0) !important;
}

.bg-green {
  border-color: green !important;
}

.center {
  text-align: center;
}

.container-grid {
  display: grid;
  grid-template-columns: repeat(3, 40px);
  grid-template-rows: repeat(3, 40px);
  align-items: center;
  justify-content: center;
}

.bms-cell {
  width: 95%;
  height: 95%;
  margin: auto;
  color: white;
  background-color: #222222;
  border: 3px solid;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.bms-cell:hover {
  -webkit-transform: scale(1.05) translateX(2px) translateY(-1px);
          transform: scale(1.05) translateX(2px) translateY(-1px);
  background-color: #111111;
  transition: all 0.2s ease-in-out;
}

.sub-section {
  background: #292c31;
  box-shadow: 1px 1px 2px rgba(64, 68, 76, 0.3),
    -1px -1px 2px rgba(18, 20, 22, 0.5),
    inset -3px 3px 6px rgba(18, 20, 22, 0.2),
    inset 3px -3px 6px rgba(18, 20, 22, 0.2),
    inset -3px -3px 6px rgba(64, 68, 76, 0.9),
    inset 3px 3px 8px rgba(18, 20, 22, 0.9);
  border-radius: 1rem;
  padding: 1.5rem;
  text-align: center;
  font-family: "Exo", sans-serif;
  background-color: #292c31 !important;
  transition: 0.2s ease;
}

.sub-section:hover {
  cursor: pointer;
  box-shadow: inset 0 8px 16px 0 rgba(0, 0, 0, 0.8);
}

.sub-sub-section {
  box-shadow: 1px 1px 2px rgba(64, 68, 76, 0.3),
    -1px -1px 2px rgba(18, 20, 22, 0.5),
    inset -3px 3px 6px rgba(18, 20, 22, 0.2),
    inset 3px -3px 6px rgba(18, 20, 22, 0.2),
    inset -3px -3px 6px rgba(64, 68, 76, 0.9),
    inset 3px 3px 8px rgba(18, 20, 22, 0.9);
  margin: 0.5rem 1.5rem 1rem 1.5rem !important;
  border-radius: 1rem !important;
  background-color: #333333;
  padding: 1.5rem 0rem 1rem 0rem !important;
}

body h4 {
  font-size: large;
}

.menu-button {
  font-size: 30px;
  color: white;
}

.menu-button:hover {
  color: rgb(120, 118, 118);
